.bonus-round {
    padding: 40px;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    color: #333;
    background-color: #f0f4f8; /* Light background for contrast */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

.bonus-round h2 {
    color: #ff69b4; /* Title color */
    margin-bottom: 20px; /* Space below the title */
}

.bonus-round h3 {
    color: #ff1493; /* Final task color */
    margin: 20px 0; /* Space above and below the task */
}

.button-container {
    margin-top: 20px; /* Space above buttons */
}

.back-button, .next-button {
    background-color: #ff69b4; /* Button background color */
    color: white; /* Button text color */
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    text-decoration: none; /* Remove underline */
    margin: 0 10px; /* Space between buttons */
    font-size: 1.2rem; /* Font size for buttons */
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.back-button:hover, .next-button:hover {
    background-color: #ff1493; /* Darker shade on hover */
    transform: scale(1.05);
}
